<script>
  import { formatDate as utilsFormatDate } from "timeUtils";
  import { auth, activities } from "../stores";
  import { dateFormat } from "../utils";
  import Spinner from "../components/layout/Spinner.svelte";

  function getName(user) {
    return user.email === $auth.user ? "you" : user.name;
  }

  function formatDate(isoDate) {
    // Since our date comes in utc format we need to create it this way to fix days being off by one due to tz offset
    var parts = isoDate.split("-");
    return utilsFormatDate(
      new Date(parts[0], parts[1] - 1, parts[2]),
      dateFormat
    );
  }
</script>

<div class="content">
  {#if $activities.loading}
    <Spinner />
  {:else}
    <h2 class="title">Previous Activities</h2>
    <ul>
      {#each $activities.items.slice(0, 10) as { users, game, status, ...activity }}
        <li>
          {game.name} was {status.name.toLowerCase()} by {getName(users[0])}
          {#if users[1]}and {getName(users[1])}{/if}
          on {formatDate(activity.createdAt)}
        </li>
      {/each}
    </ul>
  {/if}
</div>
