import {
  derived,
  writable
} from 'svelte/store';

import {
  auth
} from './Auth';

export function cacheResults(fetchData, cacheClear) {
  let cachedItems = [];

  const stores = cacheClear ? [auth, cacheClear] : [auth];

  return derived(stores, async ([$auth, $cacheBust], set) => {
    if ($cacheBust) {
      cachedItems = [];
      return;
    }
    if (!$auth.token) {
      set({
        loading: false,
        items: []
      });
      return;
    }

    if (cachedItems.length) {
      return
    }

    set({
      loading: true,
      items: []
    });
    cachedItems = await fetchData($auth.token);

    set({
      loading: false,
      items: cachedItems
    });
  }, []);
}

export function cacheBuster() {
  const {
    subscribe,
    set
  } = writable(false);

  return {
    subscribe,
    clear: () => {
      set(true);
      set(false);
    }
  }
}