import {
  get as getStore
} from 'svelte/store';

import {
  get,
  post
} from '../utils/ajax';
import {
  auth
} from './Auth';
import {
  cacheResults,
  cacheBuster
} from './cache';

function createActivityStore() {
  const buster = cacheBuster();

  const {
    subscribe
  } = cacheResults((token) => get('/activities', token), buster);

  return {
    subscribe,
    create: async (data) => {
      await post(
        "/activities", data,
        getStore(auth).token
      );

      buster.clear();
    },
  }
}
export const activities = createActivityStore();