<script>
  import Datepicker from "svelte-calendar";
  import Spinner from "../components/layout/Spinner.svelte";
  import { games, friends, statuses, activities } from "../stores";
  import { dateFormat } from "../utils";

  let gameId;
  let isReplay = false;
  let statusId;
  let selectedDate;
  let otherPlayerId;

  function submit() {
    const createdAt = selectedDate
      ? selectedDate.toISOString().slice(0, 10)
      : null;
    const additionalUserIds = otherPlayerId ? [otherPlayerId] : [];

    activities.create({
      gameId,
      isReplay,
      statusId,
      createdAt,
      additionalUserIds
    });
  }
</script>

<h1 class="title">New Activity</h1>

{#if $games.loading || $statuses.loading}
  <Spinner />
{:else}
  <form on:submit|preventDefault={submit}>

    <div class="field">
      <label class="label">What game did you play?</label>
      <div class="control">
        <div class="select">
          <select bind:value={gameId}>
            {#each $games.items as game}
              <option value={game.id}>{game.name}</option>
            {/each}
          </select>
        </div>
        <!-- <Autocomplete
        items={$games}
        labelFieldName="name"
        valueFieldName="id"
        bind:value={gameId} /> -->
      </div>
    </div>

    <div class="field">
      <div class="control">
        <label class="checkbox">
          <input type="checkbox" bind:checked={isReplay} />
          Are you replaying it?
        </label>
      </div>
    </div>

    <div class="field">
      <label class="label">What is the status?</label>
      <div class="control">
        <div class="select">
          <select bind:value={statusId}>
            {#each $statuses.items as status}
              <option value={status.id}>{status.name}</option>
            {/each}
          </select>
        </div>
        <!-- <Autocomplete
        items={$statuses}
        labelFieldName="name"
        valueFieldName="id"
        placeholder="Status"
        bind:value={statusId} /> -->
      </div>
    </div>

    <div class="field">
      <label class="label">Did you play with anyone else?</label>
      <div class="control">
        <div class="select">
          <select bind:value={otherPlayerId}>
            <option value={undefined}>No</option>
            {#each $friends.items as friend}
              <option value={friend.id}>{friend.name}</option>
            {/each}
          </select>
        </div>
      </div>
    </div>

    <div class="field">
      <label class="label">What day did this happen?</label>
      <div class="control">
        <Datepicker bind:selected={selectedDate} format={dateFormat} />
      </div>
    </div>

    <div class="field">
      <div class="control">
        <button
          class="button is-link"
          type="submit"
          disabled={!gameId || !statusId}>
          Create
        </button>
      </div>
    </div>
  </form>
{/if}
