import {
  writable,
  derived
} from 'svelte/store';

const AUTH_TOKEN_KEY = 'auth_token_key';

function createAuth() {
  const defaultAuth = {
    user: null,
    token: '',
  }

  const storedToken = JSON.parse(sessionStorage.getItem(AUTH_TOKEN_KEY));

  const {
    subscribe,
    set
  } = writable(storedToken || defaultAuth);

  return {
    subscribe,
    login: (user, token) => {
      sessionStorage.setItem(AUTH_TOKEN_KEY, JSON.stringify({
        user,
        token
      }));

      set({
        user,
        token
      });
    },
    logout: () => {
      sessionStorage.removeItem(AUTH_TOKEN_KEY);
      set(defaultAuth);
    }
  };
}

export const auth = createAuth();
export const isLoggedIn = derived(auth, ({
  token
}) => Boolean(token));